@import '../../styles/utils/mixins';
.navigation {
  display: flex;
  align-items: center;
  gap: 14px;
  padding: 13px 16px 13px 16px;
  @include tablet {
    gap: 390px;
    padding: 13px 32px 13px 32px;
  }

  @include laptop {
    gap: 590px;
    padding: 13px 60px 13px 60px;
  }
  
  @include desktop {
    gap: 856px;
    padding: 13px 695px 13px 695px;
    background-color: red;
  }
}
.navigationMenu {
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.navigationMenuItem {
  width: 100px;
  height: 34px;
  background-color: var(--primary-color);
  display: flex;
  align-items: center;
  border-radius: 40px;
  justify-content: center;

  font-size: 16px;
  line-height: 26px;
}
