@mixin mobile {
    @media screen and (min-width: 360px) {
      @content;
    }
  }
  
//   @mixin mobile-only {
//     @media screen and (max-width: 767px) {
//       @content;
//     }
//   }
  
  @mixin tablet {
    @media screen and (min-width: 768px) {
      @content;
    }
  }
  
  @mixin laptop {
    @media screen and (min-width: 1024px) {
      @content;
    }
  }

  @mixin desktop {
    @media screen and (max-width: 2560) {
      @content;
    }
  }